'use client'

import PhotoGallery from '@/app/components/shared/PhotoGallery';
import { useState } from 'react';
import Image from 'next/image';
import { FaCamera } from 'react-icons/fa';

interface Photo {
  id: number;
  src: string;
  alt: string;
  width: number;
  height: number;
  category: string;
  date?: string;
}

const categories = [
  'All',
  'Me',
  'Sea',
  'Friends',
  'Events',
  'Nature',
  'Tech',
  'Cats',
  'Food',
  'Explore',
  'Meme'
];

export default function PhotosPage() {
  const [photoIndex, setPhotoIndex] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [currentCategory, setCurrentCategory] = useState<string>('All');

  // Make categories sticky and scrollable horizontally
  return (
    <main className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Photo Gallery</h1>
      {/* Category Filter */}
      <div className="sticky top-16 z-20 -mx-4 px-4 py-4 bg-white/95 backdrop-blur-sm shadow-sm">
        <div className="flex flex-nowrap gap-3 overflow-x-auto pb-2 scrollbar-hide">
          {categories.map(category => (
            <button
              key={category}
              onClick={() => setCurrentCategory(category)}
              className={`
                px-4 py-2 rounded-lg transition-all duration-200 whitespace-nowrap
                ${currentCategory === category
                  ? 'bg-gray-900 text-white'
                  : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                }
              `}
            >
              {category}
            </button>
          ))}
        </div>
      </div>

      {/* Photo Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 mt-6">
        {filteredPhotos.map((photo, index) => (
          <div
            key={photo.id}
            className="relative aspect-square cursor-pointer group"
            onClick={() => {
              setPhotoIndex(index);
              setIsOpen(true);
            }}
          >
            <Image
              src={photo.src}
              alt={photo.alt}
              fill
              className="object-cover rounded-lg transition-transform duration-300 group-hover:scale-105"
              sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw"
            />
            <div className="absolute inset-0 bg-black/30 opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-lg flex items-center justify-center">
              <FaCamera className="w-8 h-8 text-white" />
            </div>
          </div>
        ))}
      </div>

      {/* Lightbox remains the same */}
    </main>
  );
}